import React from "react";
import Tour from "./Tour";

const Tours = ({ data, filterData }) => {
	return (
		<main>
			<header>
				<h3 className="title">Our Tours</h3>
				<div className="underline"></div>
			</header>
			<div className="tours">
				{data &&
					data.map((item) => {
						return (
							<Tour
								filterData={filterData}
								key={item.id}
								data={item}
							/>
						);
					})}
			</div>
		</main>
	);
};

export default Tours;
