import React, { useState, useEffect } from "react";
import "./App.css";
import Loading from "./Loading";
import Tours from "./Tours";
import { useFetch } from "./hooks/useFetch";

function App() {
	const { data, IsPending, error, filterData, setUrl } = useFetch();
	if (data && data.length === 0) {
		return (
			<main>
				<h1 className="title">No Tours Left</h1>
				<button
					className="refresh-btn"
					onClick={() => {
						console.log(1);
						setUrl("http://localhost:3000/trips");
					}}>
					Refresh
				</button>
			</main>
		);
	}
	return (
		<main>
			<h1 className="title">Tours Project Setup</h1>
			{IsPending && <Loading />}
			{error && <div>{error}</div>}
			<Tours data={data} filterData={filterData}></Tours>
		</main>
	);
}

export default App;
